/**********************
 Header style
 **********************/
.header {
  padding-bottom: 20px; }

.header__hero {
  width: 100%;
  overflow: hidden;
  height: 550px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 50%; }

/* Header search menu item */
.open-search {
  /* Remove "hover" and "active" menu items backgrounds... */
  /* ...and just add color */ }
  .open-search a {
    font-size: 0 !important;
    width: 30px;
    position: relative; }
    .open-search a:before {
      display: block;
      position: absolute;
      left: 0;
      top: 50%;
      line-height: 1em;
      margin-top: -0.5em;
      font-family: "zbradu-icons";
      font-style: normal;
      font-weight: 400;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      content: "";
      font-size: 28px;
      z-index: 1; }
  .open-search:hover:after, .open-search.active:after {
    display: none !important; }
  .open-search:hover a:before,
  .open-search.active a:before {
    color: red; }

