/* Header search form */
.search-form-container {
  z-index: 100;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #8A2BE2;
  padding: 20px; }
  .search-form-container .container {
    top: 50%;
    position: relative; }
  .search-form-container .tpl-close-trigger {
    background: none;
    cursor: pointer;
    position: relative;
    width: 26px;
    height: 26px;
    position: absolute;
    right: 15px;
    top: 15px;
    right: 24px;
    top: 35px; }
    .search-form-container .tpl-close-trigger:before, .search-form-container .tpl-close-trigger:after {
      background: #fff;
      width: 26px;
      margin-top: 13px;
      content: '';
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 0;
      display: block;
      height: 3px;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg); }
    .search-form-container .tpl-close-trigger:after {
      -webkit-transform: rotate(135deg);
      transform: rotate(135deg); }

/**********************
 Overwrite bootstrap
 **********************/
.header .navbar {
  border-radius: 0;
  background-color: #000;
  border: none; }

.navbar-default .navbar-nav {
  margin-left: -5px; }

.navbar-default .navbar-nav > li {
  position: relative;
  margin-left: 5px;
  margin-right: 5px; }
  .navbar-default .navbar-nav > li > a {
    font-family: "Chewy", cursive;
    text-transform: uppercase;
    font-size: 34px;
    padding-top: 25px;
    padding-bottom: 25px;
    color: #fff;
    z-index: 50; }
    .navbar-default .navbar-nav > li > a:hover {
      color: #fff; }

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:focus,
.navbar-default .navbar-nav > .active > a:hover {
  color: red;
  background-color: transparent; }

/* Menu items background graphics - hover */
.navbar-default .navbar-nav > li:hover:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  z-index: 49;
  height: 20px;
  top: 25px;
  border-top: 8px solid red;
  border-bottom: 8px solid red;
  -webkit-transform: rotate(4deg);
  transform: rotate(4deg); }

/* Menu items background graphics - active */
.navbar-default .navbar-nav > li.active:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  z-index: 49;
  background-color: #fff;
  height: 30px;
  top: 20px;
  border: 4px solid #fff;
  border-radius: 5px;
  -webkit-transform: rotate(-4deg);
  transform: rotate(-4deg); }
